<template>
  <div class="rain-figure-container">
    <div style="position: relative">
      <img ref="rainFigure" class="rain-image" :src="imageUrl" alt="" @load="resizeCanvas"/>
      <canvas ref="rainCanvas" class="rain-canvas"></canvas>
    </div>
  </div>
</template>

<script>
export default {
  name: "RainFigure",
  data() {
    return {
      drops: [],
      animationId: null,
      handleResize: null,
      imageUrl: require('../assets/image/figure.jpg')
    };
  },
  mounted() {
    this.initCanvas();

    // 保存函数引用用于移除监听器
    this.handleResize = this.resizeCanvas;
    window.addEventListener("resize", this.handleResize);
    // 使用 nextTick 确保 DOM 已渲染
    this.$nextTick(() => {
      this.resizeCanvas(); // 初始调整 canvas 大小
      this.createDrops(100);
      this.animate();
    });
  },
  beforeUnmount() {
    cancelAnimationFrame(this.animationId);
  },
  methods: {

    initCanvas() {
      const canvas = this.$refs.rainCanvas;
      this.ctx = canvas.getContext("2d");
    },
    async resizeCanvas() {
      const canvas = this.$refs.rainCanvas;
      const container = this.$refs.rainFigure;
      if (!canvas || !container) {
        console.warn("Canvas or container not available");
        return;
      }
      const containerWidth = container.offsetWidth;
      const containerHeight = container.offsetHeight;
      const dpr = window.devicePixelRatio || 1;
      // 设置 canvas 像素尺寸（乘以 dpr）
      canvas.width = containerWidth * dpr;
      canvas.height = containerHeight * dpr;

      // 设置 canvas 显示尺寸（CSS 尺寸）
      canvas.style.width = `${containerWidth}px`;
      canvas.style.height = `${containerHeight}px`;

      this.ctx.scale(dpr, dpr);
    },
    createDrops(count) {
      for (let i = 0; i < count; i++) {
        this.drops.push(new Drop(this.$refs.rainCanvas));
      }
    },
    animate() {
      const {ctx, $refs} = this;
      const canvas = $refs.rainCanvas;

      ctx.clearRect(0, 0, canvas.width, canvas.height);

      this.drops.forEach((drop) => {
        drop.update();
        drop.draw(ctx);
      });

      this.animationId = requestAnimationFrame(() => this.animate());
    },
  }
};

// 雨滴类
class Drop {
  constructor(canvas) {
    this.x = Math.random() * canvas.width;
    this.y = Math.random() * canvas.height - canvas.height;
    this.length = Math.random() * 20 + 10;
    this.speed = Math.random() * 5 + 10;
    this.canvasHeight = canvas.height;
    this.canvasWidth=canvas.width;
  }

  draw(ctx) {
    ctx.beginPath();
    ctx.moveTo(this.x, this.y);
    ctx.lineTo(this.x, this.y + this.length);
    ctx.strokeStyle = `rgba(255, 255, 255, ${Math.random() * 0.5 + 0.2})`;
    ctx.lineWidth = 1;
    ctx.stroke();
  }

  update() {
    this.y += this.speed;
    if (this.y > this.canvasHeight) {
      this.y = -this.length;
      this.x = Math.random() * this.canvasWidth;
    }
  }
}
</script>

<style scoped>
.rain-figure-container {
  position: relative;
  width: 100%;
  height: var(--content-height-1);
  display: flex;
  justify-content: center;
}

.rain-image {
  max-height: 100%;
  max-width: 100%;
}

.rain-canvas {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: transparent;
}

.mask-layer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0); /* 完全透明 */
  z-index: 10;
  pointer-events: auto; /* 可根据需要控制是否拦截事件 */
}

@media screen and (max-width: 768px) {
  .rain-figure-container {
    display: block;
  }

}
</style>